___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: 'Segoe UI', sans-serif;

  background-color: #313131;

  margin: 0;
}

.conteudo {
  display: flex;
  flex-direction: column;

  padding: 0 5%;
}

h1 {
  font-size: 3rem;
  font-weight: 700;
  color: #f9f9f9;
}

.descricao {
  font-size: 22px;
  font-weight: 600;
  color: #f9f9f9;
}

.container-formulario {
  display: flex;
  /* justify-content: center; */
  gap: 5px;

  padding: 1%;
}

.pesquisa {
  outline: 1px solid #f9f9f9;

  border:none;
  border-radius: 10px;

  font-size: 18px;

  padding: 5px;
}

.pesquisa-erro {
  outline: 2px solid #DA471B !important;
}

.btn {
  display: grid;
  padding: 15px;

  border: none;
  border-radius: 10px;
}

.btn:hover {
  opacity: 0.9;
  cursor: pointer;
  outline: 1px solid #ffffff;
}

.btn-pesquisar {
  background-color: #4DAD5B;
  color: white;
}

.btn-limpar {
  background-color: #DA471B;
  color: white;
}

.card-pokemon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;

  height: 250px;

  background-color: #e1e1e1;
  color: #313131;

  border-radius: 1rem;

  margin-top: 5%;
}

.card-pokemon:hover{
  cursor: pointer;
  outline: 1px solid red;
}

.notificacao {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  margin-left: auto;
  margin-right: auto;

  text-align: center;
  font-size: 20px;
  font-weight: 600;
  
  background-color: #DA471B;
  border-radius: 10px;

  width: 300px;

  padding: 10px 20px;
}

.notificacao:hover {
  opacity: 0.9;
  cursor: pointer;
}

.grid-pokemons {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 10px;
}`, "",{"version":3,"sources":["webpack://./src/views/pokemon-listagem/pokemon-listagem.css"],"names":[],"mappings":"AAAA;EACE,mCAAmC;;EAEnC,yBAAyB;;EAEzB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,sBAAsB;;EAEtB,aAAa;AACf;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,6BAA6B;EAC7B,QAAQ;;EAER,WAAW;AACb;;AAEA;EACE,0BAA0B;;EAE1B,WAAW;EACX,mBAAmB;;EAEnB,eAAe;;EAEf,YAAY;AACd;;AAEA;EACE,qCAAqC;AACvC;;AAEA;EACE,aAAa;EACb,aAAa;;EAEb,YAAY;EACZ,mBAAmB;AACrB;;AAEA;EACE,YAAY;EACZ,eAAe;EACf,0BAA0B;AAC5B;;AAEA;EACE,yBAAyB;EACzB,YAAY;AACd;;AAEA;EACE,yBAAyB;EACzB,YAAY;AACd;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,SAAS;;EAET,aAAa;;EAEb,yBAAyB;EACzB,cAAc;;EAEd,mBAAmB;;EAEnB,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,sBAAsB;AACxB;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,OAAO;EACP,QAAQ;;EAER,iBAAiB;EACjB,kBAAkB;;EAElB,kBAAkB;EAClB,eAAe;EACf,gBAAgB;;EAEhB,yBAAyB;EACzB,mBAAmB;;EAEnB,YAAY;;EAEZ,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,2DAA2D;EAC3D,SAAS;AACX","sourcesContent":["body {\n  font-family: 'Segoe UI', sans-serif;\n\n  background-color: #313131;\n\n  margin: 0;\n}\n\n.conteudo {\n  display: flex;\n  flex-direction: column;\n\n  padding: 0 5%;\n}\n\nh1 {\n  font-size: 3rem;\n  font-weight: 700;\n  color: #f9f9f9;\n}\n\n.descricao {\n  font-size: 22px;\n  font-weight: 600;\n  color: #f9f9f9;\n}\n\n.container-formulario {\n  display: flex;\n  /* justify-content: center; */\n  gap: 5px;\n\n  padding: 1%;\n}\n\n.pesquisa {\n  outline: 1px solid #f9f9f9;\n\n  border:none;\n  border-radius: 10px;\n\n  font-size: 18px;\n\n  padding: 5px;\n}\n\n.pesquisa-erro {\n  outline: 2px solid #DA471B !important;\n}\n\n.btn {\n  display: grid;\n  padding: 15px;\n\n  border: none;\n  border-radius: 10px;\n}\n\n.btn:hover {\n  opacity: 0.9;\n  cursor: pointer;\n  outline: 1px solid #ffffff;\n}\n\n.btn-pesquisar {\n  background-color: #4DAD5B;\n  color: white;\n}\n\n.btn-limpar {\n  background-color: #DA471B;\n  color: white;